/* App.css */



.container {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
  padding: 20px;
}

.title {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #333;
}

.start-button {
  background-color: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin-bottom: 20px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.start-button:hover {
  background-color: #45a049;
}

.timer {
  font-size: 1.5rem;
  margin-bottom: 20px;
  color: #555;
}

.sentence {
  font-size: 1.2rem;
  margin-bottom: 20px;
  color: #000000;
}

.input-container {
  margin-bottom: 20px;
}

.input-field {
  padding: 10px;
  width: 100%;
  box-sizing: border-box;
  border-radius: 5px;
  border: 1px solid #ccc;
  transition: border-color 0.3s ease;
}

.input-field:focus {
  border-color: #4CAF50;
}

.game-over {
  font-size: 1.5rem;
  color: #f44336;
  margin-bottom: 20px;
  transition: color 0.3s ease;
}

.game-over p {
  margin: 5px 0;
}

.game-over:hover {
  color: #d32f2f;
}


.flex-container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333;
  color: white;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}

/* New styles for SQLite integration */
.user-form {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
  border: 2px solid #e0e0e0;
}

.user-form h2 {
  color: #333;
  margin-bottom: 15px;
}

.game-info {
  background-color: #f0f8ff;
  padding: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
  border: 2px solid #b0d4f1;
}

.stats {
  background-color: #fff;
  padding: 15px;
  border-radius: 8px;
  margin: 15px 0;
  border: 1px solid #ddd;
}

.stats h3 {
  color: #333;
  margin-bottom: 10px;
}

.stats p {
  margin: 5px 0;
  color: #666;
}

.toggle-button {
  background-color: #f0f0f0;
  color: #666;
  border: 1px solid #ddd;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  margin-top: 10px;
  transition: background-color 0.3s;
}

.toggle-button:hover {
  background-color: #e0e0e0;
}

/* Game Actions */
.game-actions {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 20px;
}

.game-over-actions {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 20px;
}

.leaderboard-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 12px 24px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(102, 126, 234, 0.3);
}

.leaderboard-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(102, 126, 234, 0.4);
}

.leaderboard-button:active {
  transform: translateY(0);
}